import './Page404.css'
import {Link, useLocation} from 'wouter'
import { useEffect } from 'react';

export default function Page404({params}){
    const setLocation = useLocation()[1];
    useEffect(()=>{
        setTimeout(()=>{
            setLocation('/'); 
        },3500)
    },[setLocation])

    return(
        <div className="contpg404"><h1>404</h1>
            <p>
            <strong>Ups!, La pagina {params.rest} no existe!</strong>
            </p>
            <Link to="/">Ir a RedPack</Link>
        </div>
        
    )
}