import './Track.css'
export default function Track ({data = []}){
    if (data.length===0)
        return(<strong>Sin datos.</strong>)
    return (<div className="container">
    <h2>{data[0].TipoBulto}</h2>
    <ul className="responsive-table">
      <li className="table-header">
        <div className="col col-2">Estado</div>
        <div className="col col-2">Fecha</div>
      </li>
      {
          data.map((mov,pos)=>{
              return(
                    <li key={pos} className="table-row">
                        <div className="col col-2" data-label="Estado"><b>{mov.Estado}</b></div>
                        <div className="col col-2" data-label="Fecha">{mov.Fechaestado}</div>
                    </li>
        
              )
          })
      }

    </ul>
  </div>)
}
