const pathbase = 'https://webservices.buseslep.com.ar:443/WebServices/';


const CreateReq =   function (urn, method, parameters, auth){
            var a = btoa("UsuarioLep");
            var b = btoa("Lep1234");
                
            var soap_request = '<?xml version="1.0" encoding="UTF-8" standalone="no"?>' +
                '<SOAP-ENV:Envelope ' +
                'xmlns:SOAP-ENV="http://schemas.xmlsoap.org/soap/envelope/" ' +
                'xmlns:xs="http://www.w3.org/2001/XMLSchema" ' +
                'xmlns:tns="http://tempuri.org/" ' +
                'xmlns:soap="http://schemas.xmlsoap.org/wsdl/soap/" ' +
                'xmlns:soapenc="http://schemas.xmlsoap.org/soap/encoding/" ' +
                'xmlns:mime="http://schemas.xmlsoap.org/wsdl/mime/" ' +
                'xmlns:xsi="http://www.w3.org/2001/XMLSchema-instance" ' +
                'xmlns:xsd="http://www.w3.org/2001/XMLSchema" >' +
                '<SOAP-ENV:Body>' +
                '<mns:' + method + ' xmlns:mns="urn:' + urn + '" SOAP-ENV:encodingStyle="http://schemas.xmlsoap.org/soap/encoding/">';
            if (auth) {
                soap_request += 
                    '<userWS xsi:type="xs:string">' + atob(a) + '</userWS>' +
                    '<passWS xsi:type="xs:string">' + atob(b) + '</passWS>';
            }

            for (let i = 0; i< parameters.length; i++){
                var current = parameters[i];
                if (parameters[i].n)
                  soap_request = soap_request + '<'+current.n+' xsi:type="xs:'+current.t+'">'+current.v+'</'+current.n+'>';
                else
                  soap_request = soap_request + '<'+current.name+' xsi:type="xs:'+current.type+'">'+current.value+'</'+current.name+'>';
            };
            soap_request += '</mns:'+method+'>' +
                '</SOAP-ENV:Body>' +
                '</SOAP-ENV:Envelope>';
            //console.log(soap_request);
            return soap_request;
    } //end call service

const XML2JSON = function(data){ 
        const ResponseError = {status:false, error:'unexpected response'};   
        const beginMark = '<return xsi:type="xsd:string">';
        const endMark   = '</return>';
        let beginIndex = data.indexOf(beginMark);
        let endIndex = 0;
        if (beginIndex >0){

            beginIndex+=beginMark.length;
            endIndex =  data.indexOf(endMark);
            let salida = {status:true, data:[]};
            let midata = data.substr(beginIndex,endIndex-beginIndex);
            midata=midata.replace(/\.,/g,',');
            // fix, problem "precio":800., in schedulesGo
            try {
                salida.data= JSON.parse (midata).Data;
            } catch(e){
                salida=ResponseError;
            }
            return salida;
        } else {
            return ResponseError;
        }
    } //end XML2JSON
    
const  callService =  (method,parameters,urn,urlservice,autologin)=> { return new Promise(
    (resolve,reject)=>{
        let request_soap = CreateReq(urn,method,parameters,autologin);

        var miInit = { 
            method: 'POST',
            cache: 'default',
            body :request_soap 
        };
        fetch(urlservice,miInit).then(
           function (resp) {
               if (resp.ok)
                   return resp.text()
                else return 'Error';
            }
        ).then(
            function(response){
                //console.log(response);
                resolve(XML2JSON(response));
            }
        ).catch( function (e) {
            reject(e);
        });   
    }
    )

}


