export default function CircleRing({className='', color='#1b95d2', size=30}){
    return(
        <svg className={className} viewBox="0 0 120 120" version="1.1"  height="200px" width="200px" xmlns="http://www.w3.org/2000/svg">
          <circle cx="60" cy="60" r={size} 
          stroke={color}
          strokeWidth={7}
          fill='none'
          />
        </svg>
 )  
 }
