import { useContext } from 'react'
import _context from '../Context/ModalContextProvider'
import './Modal.css'
export default function Modal(){
    const context = useContext(_context)
    if (! context.message) return null
    return (
        <>
        <div className="mmback" >
        </div>

        <div className="mmodal">

            <div className="mm-header">
                {context.title}
            </div>
            <div className="mm-body">
                <p>
                    {context.message}
                </p>

                <button onClick={()=>context.setMessage('')}>
                    Aceptar
                </button>
            </div>
        </div>
        </>
    )
}