import {useState, useContext} from 'react'
import './ServicioIntegral.css'
import MS from '../services/MainService'
import {useLocation} from 'wouter'
import contextModal from '../Context/ModalContextProvider'
import Track from './Track'
import Spinner from '../ui/Spinner'


export default function ServicioIntegral({modeRedirect = true, defautvalue =''}){
    console.log('Render ServicioIntegral')
    

    const [numGuia, setNumGuia] = useState(defautvalue)
    const [fullNumberGuia, setFullNumberGuia] = useState('')
    const setLocation = useLocation()[1];
    const apiModal = useContext(contextModal)
    const [color, setColor] = useState('red')
    const [autoSend, setAutoSend] = useState(0)
    const [trackEnco, setTrackEnco] = useState([])
    const [isLoadingTrack, setIsLoadingTrack] = useState(false)

    



    const handleChangenumGuia = (e,flag3)=>{
        // formatCorrect: 2021-000123
        let newValue
        if (flag3 && flag3 === 'check')
            newValue=defautvalue
        else   newValue= e.target.value

        setTrackEnco([])

        let check1 = false;
        // valid 3 caracteres o 2021-125 tambien
        if (newValue.length>2 && newValue.length<12) check1 = true;
        if (check1){ // requiere dos partes '-'
            let partes = newValue.split('-')
            let prefijo =''+ new Date().getFullYear()
            let postfijo= ''
            if (partes.length<2) {postfijo = newValue}
            else if (partes.length === 2) {
                prefijo = partes[0]
                postfijo = partes[1]
            } else {
                check1 = false
            }

            if (check1)
                check1 = (prefijo.length === 4 && postfijo.length>2 && postfijo.length<7)

            if (check1){
                let aux =''
                switch(postfijo.length){ // Rellenamos con 0
                    case 3:
                        aux = '000'
                      break
                    case 4:
                        aux ='00'
                      break;
                    case 5:
                        aux ='0'
                      break
                    default:
                        aux = ''
                }
                postfijo = aux + postfijo

                check1 =  ! isNaN(postfijo)
                check1 = check1 &&  ! isNaN(prefijo)
                if (check1) setFullNumberGuia(prefijo+'-'+postfijo)
            }
    
        }

        if (!check1) {
            setColor('red')
            setFullNumberGuia('')
        }
        else setColor('green')
        setNumGuia(newValue)
    }

    
    const handleSubmit = (e)=>{
        if (e) e.preventDefault() //for default route

        if (isLoadingTrack){
            apiModal.setMessage('Operacion en proceso')
            return
        }
        if (fullNumberGuia.length===0){
            setFullNumberGuia('')
            apiModal.setMessage('Numero de Guia incorrecto!')
            return
        }
        let params = [
            { n: 'NroGuia', t: 'string', v: fullNumberGuia }
        ]

        if (modeRedirect) {
            setLocation('/seguimiento/'+fullNumberGuia)
            return 
        } else {
            setFullNumberGuia('')
            setNumGuia('')    
        }

        setIsLoadingTrack(true)
        MS.w8('BuscarEncomienda',params)
        .then((resp)=>{
            setIsLoadingTrack(false)
            if (resp.status) setTrackEnco(resp.data)
        })
        .catch((e)=>{
            apiModal.setMessage('Compruebe conexion a Internet')
            setIsLoadingTrack(false)
        })

    }



    if(defautvalue!=='' && fullNumberGuia.length===0 && autoSend===0){
        setAutoSend(1) // cargo y chequeo el numero de guia
        handleChangenumGuia(null,'check')
    }

    if(defautvalue!=='' &&  autoSend===1 && fullNumberGuia){
        handleSubmit()
        setAutoSend(2) // Ya se activo el autosend
    }

    return(
        <div className='cardIntegral'>
            <h4>
            Servicio integral de Encomiendas
    y logística RÁPIDO, CONFIABLE
    y a un PRECIO inmejorable
            </h4>
            <form onSubmit={handleSubmit}>
                <input type='text'
                     placeholder='Seguimiento - escriba el número de envío'
                     className='inpcode'
                value={numGuia} onChange={handleChangenumGuia}
                style={{
                    color: color
                }}/>
                <input type='submit' className='btncode' value="Consultar" />
            </form>
            {isLoadingTrack &&  <div className="isloadtrack">
                        <Spinner color='#FFF'/>
                        <label>Buscando Encomienda </label>
                        </div>                        
             }

            {trackEnco.length>0 && <Track data={trackEnco}/>}
        </div>
    )
}
