import MenuIcon from '../ui/MenuIcon'
import './NavMain.css'
import { Link } from "wouter"

export default function NavMain(){
    const logoURL = '/img/logoredpack.png'
    return(
        <nav className="NavMain">
          <div className="nmlo hxs">
            <Link to="/">
              <img src={logoURL} alt='Logo' className="ltohand"/>
            </Link>
          </div>
          <MenuIcon className="nmisan hnxs" color="#222" />
          <div className='nmli'>
            <Link to="/seguimiento">Seguimiento envio</Link>
            <Link to="/servicio">El servicio</Link>
            <Link to="/seguro">Seguro</Link>
            <Link to="/beneficios">Beneficios</Link>
            <Link to="/contacto" className="optcontact">Contacto</Link>             
          </div>
        </nav>
    )
}