import {callService,CONFIG} from './Config';
  
const MainService = { // object mainservice

    w8: (method,parameters) => {
        const {autU,autP,urn,url} = CONFIG.WebServiceLep8;
        parameters.push(autU,autP);
        return callService(method,parameters,urn,url,false);
    }


}

export default  MainService;