import Encomiendas1 from "../Components/Encomienda";
import Footer from "../Components/Footer";
import NavMain from "../Components/NavMain";
import ServicioIntegral from "../Components/ServicioIntegral";
import LayPage from "../Layouts/LayPage";
import {useState} from 'react'

export default function Encomiendas(props){
    let name = props.params.name ? props.params.name : ''
    const [numEncomienda] = useState(name)
    //const setLocation = useLocation()[1];
    console.log('Render page Encomiendas: '+name)

    return(
        <LayPage>
            <NavMain />
            <main className='layMain'>
                <ServicioIntegral modeRedirect={false} defautvalue={numEncomienda}/>
                <Encomiendas1 />
            </main>
            <Footer />
        </LayPage>
    )
}