import './Spinner.css'
export default function Spinner({color='#333'}) {
    return(
    <svg className="spinner" viewBox="0 0 16 16" fill="none" data-view-component="true" width="32" height="32" >
        <circle cx="8" cy="8" r="7" stroke={color} strokeOpacity="0.25" strokeWidth="2" vectorEffect="non-scaling-stroke"></circle>
        <path d="M15 8a7.002 7.002 0 00-7-7" stroke={color} strokeWidth="2" strokeLinecap="round" vectorEffect="non-scaling-stroke"></path>
    </svg>
    )
}