import './Footer.css'

import { useState, useContext } from 'react'
import CircleCeleste from '../ui/CircleCeleste'
import CircleImage from '../ui/CircleImage'
import EmailIcon from '../ui/EmailIcon'
import Spinner from '../ui/Spinner'
import WebIcon from '../ui/WebIcon'
import WhatsApp from '../ui/WhatsAppIcon'
import ModalContext from '../Context/ModalContextProvider'
export default function Footer(){
    const [inmutar,setInmutar] = useState(false); 
    const [isSend,setIsSend] = useState(false); 

    const [name,setName] = useState('');
    const [message,setMessage] = useState('');

    const context = useContext(ModalContext)
    const handleSubmit = (e)=>{
        e.preventDefault();

        if (name.length<2 || message.length<2){
            context.setMessage('Ingrese un nombre y mensaje valido')
            return true;
        }
        setInmutar(true);
        setIsSend(false);


        setTimeout(() => {
            setIsSend(true);
            setInmutar(false);
            setName('');
            setMessage('');

        }, 2000);
    }

    return (
        <div className="confoo">
            <div className="focol1 hxs" >
                <div style={{position:'relative'}}>
                    <CircleImage className="foimgcir" color='#a0a0a0'/>
                </div>
            </div>
            <div className="foocol" >
                      <h1 className="fotitle">CONTACTO</h1> 
               <ul className='foul1'>
                    <li><WhatsApp color='#FFF' />
                    351-2466600
                    </li>
                    <li><EmailIcon color='#FFF' />
                    expreso@buseslep.com.ar
                    </li>
                    <li>
                        <WebIcon color='#FFF'/>
                    www.buseslep.com.ar
                    </li>

                </ul>

            </div>
            <div className="foocol">

                <form className="foform" onSubmit={handleSubmit}>
                    <label htmlFor="nombre">Nombre</label>
                    <input type='text' id="nombre" value={name}
                     onChange={x=>setName(x.target.value)}
                     readOnly={inmutar}
                     />
 
                    <label htmlFor="mensaje">Mensaje</label>
                    <textarea id="mensaje" rows="8" value={message} 
                    onChange={x=>setMessage(x.target.value)}
                    readOnly={inmutar}
                    />
  
                    {inmutar &&  <>
                        <label>Enviando mensaje</label>
                        <Spinner />
                        </>                        
                    }

                    {isSend && 
                        <label><b>Mensaje enviado exitosamente!</b></label>                
                    }

                    <button style={{display: inmutar?'none':'block'}}> Enviar mensaje</button>
                </form>

            </div>
            <div className="foocol, hxs">
                <CircleCeleste  color='#aaa' />
            </div>
        </div>
    )
}